/* small screen CSS */


/* =======Variables======== */
/* Colors */
$currentLinkColor: #EE0D0D;
$linkColor: #1F3245;

/* fonts */
$contentFont: Helvetica, sans-serif;
$headerFont: 'Source Code Pro', monospace;

/* dimensions */
$wrapperWidth: 100%;
$headerHeight: 100px;
$standardMargin: 4%;


/* =======BODY================ */
body {
    background: white; /* Greetings, Joe! */
    font-family: $contentFont;
    font-size: 1.1em;
    line-height: 1.25em;

    a {
        color: $linkColor;
        }

    h1 {
        font-weight: bold;
        font-size: 1.5em;
        margin-top: $standardMargin;
        font-family: $headerFont;
        }

    h2 {
        font-weight: bold;
        font-size: 1.2em;
        margin-top: $standardMargin;
        font-family: $headerFont;
        }

    p {
        margin-top: $standardMargin;
        }

    ul {
        margin-top: $standardMargin;
        }

    em {
        font-style: italic;
        }

    strong {
        font-weight: bold;
        }

    figure {
        margin-top: $standardMargin;
        }

    }


/* =======WRAPPER============= */
#wrapper {
    width: $wrapperWidth;
    margin-left: auto;
    margin-right: auto;
    }


/* =======HEADER============== */
header {
    width: $wrapperWidth * .66;
    height: 50px;
    margin-left: $wrapperWidth * .17;
    text-align: left;
    float: left;

    img {
        height: 100%;
        float: left;
        margin-top: 10%;
    }

    h1 {
        font-size: 2em;
        margin-top: 10%;
        margin-left: $standardMargin;

        a {
            color: black;
            text-decoration: none;
            }
        }

    }



/* =======NAV================= */
nav {
    width: $wrapperWidth;
    height: 60px;
    font-family: $headerFont;
    clear: both;

    ul {
        list-style: none;
        width: 70%;
        margin-top: 11%;
        float: right;
       }

    li {
        float: left;
        width: 15%;
        }

    a {
        display: block;
        text-align:center;
    }

    .current {
	    color: $currentLinkColor;
	    }    

    }

/* =======MAIN=============== */
main {
    width: $wrapperWidth;
    padding-left: $wrapperWidth * .04;
    padding-right: $wrapperWidth * .04;
    margin-bottom: $standardMargin;
    margin-top: $standardMargin;

    img {
        width: 100%;
        }

    figcaption {
        font-style: italic;
        font-size: .75em;    
        }

    }

/* =======ASIDE============== */
aside {
    width: $wrapperWidth * .25;
    float: left;
    padding: 10px;
    margin-bottom: $standardMargin;
    }

/* ======FOOTER============== */
footer {
    clear: both;
    background: black;
    color: white;
    height: 100px;

    p {
        text-align: center;
        padding-top: 2em;
        }

    a {
        color: $currentLinkColor;
        }

    }





